import elements from "../domElements"

const quantityOfAvaliableSlots = () => {
    let total = 0

    if(elements.comparation.slotA.getAttribute('data-empty') === "true") {
        total += 1
    }

    if(elements.comparation.slotB.getAttribute('data-empty') === "true") {
        total += 1
    }

    return total;
}

export default quantityOfAvaliableSlots;