import elements from "../domElements"
import SearchItems from "../../services/SearchItems";
import updateSearchResults from "../actions/updateSearchResults";
import toggleLoader from "../helpers/toggleLoader";

const [toggleOnLoader, toggleOffLoader] = toggleLoader

const handleInput = () => {
    elements.search.searchForm.addEventListener('submit', (ev) => {
        ev.preventDefault();
        const inputValue = elements.search.input.value

        if(inputValue.length === 0) {
            return
        }

        toggleOnLoader()

        SearchItems.searchByName(inputValue).then(data => {
            updateSearchResults(data)
            toggleOffLoader()
        })
    })
}

export default handleInput