import constants from '../constants'
import items from '../mocks/items';

// Remove this
function shuffleArray(array) {
    for (var i = array.length - 1; i > 0; i--) {
        var j = Math.floor(Math.random() * (i + 1));
        var temp = array[i];
        array[i] = array[j];
        array[j] = temp;
    }

    return array
}

class SearchItems {
    async searchByName(term) {
        return fetch(`${constants.API_URL + constants.SEARCH_ENDPOINT}?q=${term}`).then(data => data.json()).then(data => window.products = data)
        // return new Promise((resolve, reject) => {
        //     const result = shuffleArray(items)
        //     window.products = result
        //     setTimeout(() => resolve(result), 500 )
        // })
    }
}

export default new SearchItems()