import elements from '../domElements'
import buildCard from '../builders/card'

let previousReference = false;

const updateSearchResults = (items) => {
    const cards = []
    
    if(previousReference && previousReference.destroy) {
        previousReference.destroy()
    }

    // If we don't have itens passed them we clear the whole thing, otherwise just show the slider
    if(!items || items.length === 0) {
        elements.search.resultsWithRefresh().innerHTML = ''
        elements.search.wrapper.classList.remove('search-open')
        // Might need to destroy old slide reference here
        return;
    } else {
        elements.search.wrapper.classList.add('search-open')
    }

    // Create item elements
    items.forEach(item => {
        const card = buildCard(item, true)
        cards.push(card)
    });

    // Clean all child
    elements.search.resultsWithRefresh().innerHTML = ''
    
    // Append items to search results
    cards.forEach(item => {
        elements.search.resultsWithRefresh().appendChild(item)
    })
    
    console.log(elements.search.resultsWithRefresh())
    // Generate slider with results
    previousReference = tns({
        container: elements.search.resultsWithRefresh(),
        items: 3,
        gutter: 30,
        mouseDrag: true,
        loop: false,
        slideBy: 1,
        nextButton: elements.search.slider.nextButtonWithRefresh(),
        prevButton: elements.search.slider.prevButtonWithRefresh(),
        responsive: {
            100: {
                items: 1
            },
            400: {
                items: 2
            },
            900: {
              items: 3
            }
        }
    });

    // return elements.search.results
}

export default updateSearchResults