import elements from "../domElements";
import getSelectedProductIds from '../helpers/getSelectedProductsIds'
import addItemToSlot from '../actions/addItemToSlot'
import items from "../../mocks/items";
import quantityOfAvaliableSlots from '../helpers/quantityOfAvaliableSlots'
import clearCheckBoxes from '../helpers/clearCheckboxes'

const addItensToSlot = () => {
    elements.search.addButton.addEventListener('click', () => {
        const selectedProductIds = getSelectedProductIds()
        const quantitySlots = quantityOfAvaliableSlots()

        if(quantitySlots === 0 && selectedProductIds.length === 2) {
            // Clear the slots and replace with new products (?)
        }

        if(quantitySlots < selectedProductIds.length) {
            alert("Você não tem espaço o suficiente para adicionar")
            return;
        }

        if (selectedProductIds.length === 0) {
            alert("Selecione algum produto")
            return;
        }

        if (selectedProductIds.length > 2) {
            alert("Selecione apenas 2 itens para realizar a comparação")
            return;
        }

        // Get items object
        const choosenOnes = window.products.filter(item => {
            return selectedProductIds.includes(item.id);
        })

        // Uncheck the checkboxes

        // Add items to slot
        choosenOnes.forEach(item => {
            addItemToSlot(item)
        })

        clearCheckBoxes();
    })
}


export default addItensToSlot;