import constants from "../../constants";
import elements from "../domElements";
import getProductsIdsInSlot from '../helpers/getProductsIdsInSlot'
import updateCompareTable from '../actions/updateCompareTable'
import compareResponse from "../../mocks/compareResponse";
import toggleLoader from "../helpers/toggleLoader";

const [toggleOnLoader, toggleOffLoader] = toggleLoader;

const handleCompareButtonSubmit = () => {
    elements.comparation.compareButton.addEventListener('click', () => {
        const selectedProductIds = getProductsIdsInSlot()

        if(!selectedProductIds.length) {
            alert("Ops! Parece que você não adicionou nenhum produto para comparar")
            return;
        }

        if(selectedProductIds.length === 1) {
            alert("Adicione dois produtos para realizar a comparação")
            return;
        }

        // Make the request to handle with
        // new Promise((resolve, reject) => {
        //     toggleOnLoader()
        //     setTimeout(() => { 
        //         resolve(compareResponse)
        //     }, 200)
        // }).then(data => {
        //     // Call build table to interact with interface
        //     toggleOffLoader()
        //     updateCompareTable(compareResponse)
        // })
        
        toggleOnLoader()
        fetch(`${constants.API_URL + constants.COMPARE_ENDPOINT}?ids=${selectedProductIds.join(',')}`)
            .then(data => data.json())
            .then(data => {
                toggleOffLoader()
                updateCompareTable(data)
        })
    })
}


export default handleCompareButtonSubmit;