import elements from "../domElements"

const getProductsIdsInSlot = () => {
    const selectedProductsIds = []
    // Find selected products by checkbox
    const selectedCheckboxes = elements.comparation.wrapper.querySelectorAll('.product-card')
    selectedCheckboxes.forEach(checkbox => {
        // Find product id by checkbox
        selectedProductsIds.push(checkbox.closest('.product-card').getAttribute('data-product-id'))
    })

    return selectedProductsIds
}

export default getProductsIdsInSlot;