import elements from '../domElements'
import buildCard from '../builders/card'

const addItemToSlot = (item) => {
    // Build card element
    const card = buildCard(item, false)
    
    // If first slot is empty add to it
    if(elements.comparation.slotA.getAttribute('data-empty') === 'true') {
        elements.comparation.slotA.insertBefore(card, elements.comparation.slotA.firstChild)
        elements.comparation.slotA.setAttribute('data-empty', false)
        elements.comparation.slotA.classList.toggle('is-skeleton')
    } else if(elements.comparation.slotB.getAttribute('data-empty') === 'true') {
        elements.comparation.slotB.insertBefore(card, elements.comparation.slotB.firstChild)
        elements.comparation.slotB.setAttribute('data-empty', false)
        elements.comparation.slotB.classList.toggle('is-skeleton')
    } else {
        alert("Você não tem mais espaço para adicionar items. Remova algum item e adicione novamente")
    }

}

export default addItemToSlot