import elements from './../domElements'
import buildSpec from './spec'
{/* <div class="spec">
    <span class="key">Armazenamento interno</span>
    <span class="value-a">128GB</span>
    <span class="value-b">32GB</span>
</div> */}

const buildSpecTable = (groupedResults) => {
    // Remove all previous items
    elements.comparation.specs.innerHTML = ''

    const specs = []

    groupedResults.forEach(item => {
        const spec = buildSpec(item)

        specs.push(spec)
        elements.comparation.specs.appendChild(spec)
    })


    return specs
}

export default buildSpecTable